html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: Roboto, Helvetica, sans-serif;
}

h1,
h2,
h3 {
  margin-top: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

#accordionSitemap .accordion-button::after {
  margin-left: 0.5rem;
}
#accordionSitemap .accordion-button:focus {
  box-shadow: none;
  border-color: rgba(0, 0, 0, 0.125);
}
#accordionSitemap .accordion-button:not(.collapsed) {
  color: unset;
  background: none;
  box-shadow: none;
}
#accordionSitemap .accordion-button:not(.collapsed)::after {
  background-image: var(--bs-accordion-btn-icon);
}

.card {
  padding: 0.9375rem;
}

/* CUSTOMIZE THE CAROUSEL
-------------------------------------------------- */
/* Carousel base class */
.carousel {
  width: 100%;
}

/* Since positioning the image, we need to help out the caption */
.carousel-caption {
  top: 50%;
  bottom: 0;
  z-index: 10;
  transform: translateY(-50%);
}

/* Declare heights because of positioning of img element */
.carousel-item {
  height: 32rem;
}

@media (min-width: 576px) {
  .nav--divider .nav-item:not(:last-child) .nav-link {
    border-right: var(--bs-border-width) var(--bs-border-style) var(--bs-border-color) !important;
  }
}

.sidebar {
  width: 100%;
}
@media (min-width: 768px) {
  .sidebar {
    width: 6rem;
  }
}
.sidebar__ul {
  overflow: auto;
}
.sidebar__li a {
  width: 25vw;
}
@media (min-width: 768px) {
  .sidebar__li a {
    width: auto;
    border-bottom: var(--bs-border-width) var(--bs-border-style) var(--bs-border-color) !important;
  }
}

.toggle {
  padding: 0.625rem;
}